import React from 'react'
import styles from './sliderWithTooltip.module.scss'
import { Slider } from '@/components/ui/slider'
import useCardStore from '@/store/store'

interface PropTypes {
    childId: string
    defaultValue: number | null | undefined
}

const SliderWithTooltip = ({ childId, defaultValue }: PropTypes) => {
    const [tooltipOpen, setTooltipOpen] = React.useState(false)
    const [value, setValue] = React.useState(defaultValue ?? 16)

    const { selectedID, updateChildItem } = useCardStore()

    return (
        <div>
            <Slider
                defaultValue={[defaultValue ?? 16]}
                max={70}
                min={8}
                step={1}
                onValueChange={(value) => {
                    updateChildItem(Number(selectedID), childId, {
                        fontSize: value[0],
                    })
                    setValue(value[0])
                }}
                onMouseEnter={() => setTooltipOpen(true)}
                onMouseLeave={() => setTooltipOpen(false)}
            />
            {tooltipOpen && (
                <span className={styles.customTooltip}>{value}</span>
            )}
        </div>
    )
}

export default SliderWithTooltip
