import ElementAddDialog from '@/components/dashboardView/elements/elementAddDialog'
import useCardStore from '@/store/store'
import { getBackgroundStyle, getColorStyle } from '@/utils/getStyles'
import { debounce } from 'lodash'
import { GripHorizontal, X } from 'lucide-react'
import { useLocation } from "react-router-dom";
import React, { RefObject, useEffect, useState } from 'react'
import ReactGridLayout, { Layout, Responsive, WidthProvider } from 'react-grid-layout'
import 'react-grid-layout/css/styles.css'
import 'react-resizable/css/styles.css'
import { Rnd } from 'react-rnd'
import styles from './snapGridLayout.module.scss'

interface SnapGridPropsType {
    imageDownloadRef: RefObject<HTMLDivElement | null>
}

const ResponsiveReactGridLayout = WidthProvider(Responsive) as unknown as React.FC<ReactGridLayout.ResponsiveProps>;

const SnapGrid = ({ imageDownloadRef }: SnapGridPropsType) => {
    const useQuery = () => {
        return new URLSearchParams(useLocation().search);
    };
    const query = useQuery()

    const [columns, setColumns] = useState('')
    const [rows, setRows] = useState('')

    useEffect(() => {
        setColumns(query.get("col") || '');
        setRows(query.get("row") || '');
    }, [query]);

    const dynamicColumns = columns ?? 1
    const dynamicRows = rows ?? 1

    const totalItems = Number(dynamicColumns) * Number(dynamicRows)

    const {
        primaryColor,
        secondaryColor,
        items,
        fontWeight,
        borderRadius,
        gridGap,
        selectedID,
        layoutWidth,
        setItems,
        setSelectedID,
        updateItems,
        updateChildItem,
        removeItems,
        setLayoutWidth,
    } = useCardStore()

    useEffect(() => {
        const savedLayout = localStorage.getItem('snapGridLayout')
        const savedItems = localStorage.getItem('snapGridItems')

        if (savedLayout && savedItems) {
            setItems(JSON.parse(savedItems))
            setToolbox({
                ...toolbox,
                lg: JSON.parse(savedLayout),
            })
        } else {
            const generatedItems = Array.from(
                { length: totalItems },
                (_, index) => ({
                    id: index + 1,
                    title: `Title ${index + 1}`,
                    titleColor: primaryColor,
                    fontFamily: 'Barlow',
                    titleSize: 30,
                    titleVertical: '5',
                    titleHorizontal: '5',
                    transparent: false,
                    backgroundColor: null,
                })
            )
            setItems(generatedItems)
        }
    }, [dynamicColumns, primaryColor, setItems, totalItems])

    const [currentBreakpoint, setCurrentBreakpoint] = useState<string>('lg')
    const [mounted, setMounted] = useState(false)
    const [toolbox, setToolbox] = useState<{ [key: string]: Layout[] }>({
        lg: [],
    })

    useEffect(() => {
        setMounted(true)
    }, [])

    useEffect(() => {
        if (typeof window === 'undefined') return

        const updateWidth = () => {
            if (imageDownloadRef.current) {
                setLayoutWidth(imageDownloadRef.current.offsetWidth)
            }
        }

        updateWidth()
        window.addEventListener('resize', updateWidth)

        return () => {
            window.removeEventListener('resize', updateWidth)
        }
    }, [])

    const onBreakpointChange = (breakpoint: string) => {
        setCurrentBreakpoint(breakpoint)
        setToolbox({
            ...toolbox,
            [breakpoint]:
                toolbox[breakpoint] || toolbox[currentBreakpoint] || [],
        })
    }

    const saveLayout = (currentLayout: Layout[]) => {
        localStorage.setItem('snapGridLayout', JSON.stringify(currentLayout))
        localStorage.setItem('snapGridItems', JSON.stringify(items))
    }

    const debouncedSaveLayout = debounce(saveLayout, 300)

    const onLayoutChange = (currentLayout: Layout[]) => {
        debouncedSaveLayout(currentLayout)
    }

    const onResizeStop = (layout: Layout[]) => {
        localStorage.setItem('snapGridLayout', JSON.stringify(layout))
    }

    const generateRowLayout = () => {
        return items.map((item, index) => ({
            i: item.id.toString(),
            x: index % Number(dynamicColumns),
            y: Number(dynamicColumns),
            w: 1,
            h: 1,
        }))
    }

    const handleRemoveBox = (id: number) => {
        if (id) {
            removeItems(id)
        }
    }

    const generateDOM = () => {
        return items.map((item) => {
            return (
                <div
                    key={item.id}
                    className={`${styles.gridItem} group`}
                    style={{
                        fontWeight: fontWeight,
                        minHeight: '40px !important',
                        height: 'auto !important',
                        maxHeight: '500px !important',
                        position: 'relative',
                        textAlign: 'center',
                        borderRadius: borderRadius,
                        outline:
                            selectedID === item.id ? `2px solid #f00` : 'none',
                        ...getBackgroundStyle(
                            item.backgroundImage ??
                                item.backgroundColor ??
                                secondaryColor,
                            item.transparent
                        ),
                    }}
                    onClick={() => {
                        setSelectedID(item.id)
                    }}
                >
                    <div
                        className={`${styles.handlerItem} drag-handle group-hover:opacity-100`}
                    >
                        <GripHorizontal
                            size={20}
                            color={primaryColor ?? '#60A5FA'}
                        />
                    </div>

                    <div
                        className={`${styles.handlerItem} group-hover:opacity-100`}
                        style={{
                            top: 12,
                            right: 35,
                            cursor: 'pointer',
                        }}
                        onClick={() => handleRemoveBox(item.id)}
                    >
                        <X size={16} color={'red'} />
                    </div>

                    <div
                        className={`${styles.handlerItem} group-hover:opacity-100`}
                        style={{
                            top: '50%',
                            right: '50%',
                            transform: 'translate(50%, -50%)',
                            cursor: 'pointer',
                        }}
                    >
                        <ElementAddDialog />
                    </div>

                    <h2
                        contentEditable
                        suppressContentEditableWarning
                        onBlur={(e) => {
                            if (e.target.textContent) {
                                updateItems(item.id, {
                                    title: e.target.textContent,
                                })
                            }
                        }}
                        style={{
                            position: 'absolute',
                            top: `${item.titleVertical ?? '5'}%`,
                            transform: `translateY(-${item.titleVertical ?? '5'}%) translateX(-${item.titleHorizontal ?? '5'}%)`,
                            left: `${item.titleHorizontal ?? '5'}%`,
                            fontSize: item.titleSize,
                            fontFamily: `"${item.fontFamily}", sans-serif`,
                            whiteSpace: 'wrap',
                            lineHeight: 1.5,
                            ...getColorStyle(item.titleColor ?? primaryColor),
                        }}
                    >
                        {item.title}
                    </h2>
                    {item.children &&
                        item.children?.length > 0 &&
                        item.children.map((el, index: number) => (
                            <Rnd
                                key={index}
                                default={{
                                    x: el?.position?.positionX ?? 10,
                                    y: el?.position?.positionY ?? 10,
                                    width: el.name === 'image' ? 100 : 'auto',
                                    height: el.name === 'image' ? 100 : 'auto',
                                }}
                                onResizeStop={(
                                    e,
                                    direction,
                                    ref,
                                    delta,
                                    position
                                ) => {
                                    updateChildItem(Number(selectedID), el.id, {
                                        position: {
                                            positionX: position.x,
                                            positionY: position.y,
                                        },
                                    })
                                }}
                                onDragStop={(e, direction) => {
                                    updateChildItem(Number(selectedID), el.id, {
                                        direction: {
                                            horizontal: direction.x,
                                            vertical: direction.y,
                                        },
                                    })
                                }}
                                bounds="window"
                                dragAxis={'both'}
                                enableResizing={{
                                    top: true,
                                    right: true,
                                    bottom: true,
                                    left: true,
                                    topRight: true,
                                    bottomRight: true,
                                    bottomLeft: true,
                                    topLeft: true,
                                }}
                                onDoubleClick={() => {
                                    updateItems(item.id, {
                                        children: item.children?.filter(
                                            (child) => child.id !== el.id
                                        ),
                                    })
                                }}
                            >
                                <div key={index}>
                                    {el.name === 'text' && el.value !== '' && (
                                        <h4
                                            contentEditable
                                            suppressContentEditableWarning
                                            onBlur={(e) => {
                                                if (
                                                    e.target.textContent &&
                                                    selectedID
                                                ) {
                                                    updateChildItem(
                                                        selectedID,
                                                        el.id,
                                                        {
                                                            value: e.target
                                                                .textContent,
                                                        }
                                                    )
                                                }
                                            }}
                                            style={{
                                                fontSize: el.fontSize ?? 16,
                                                fontWeight: fontWeight,
                                                fontFamily: `"${el.fontFamily}", sans-serif`,
                                                whiteSpace: 'wrap',
                                                lineHeight: 1.5,
                                                ...getColorStyle(
                                                    el.color ?? primaryColor
                                                ),
                                            }}
                                        >
                                            {el.value}
                                        </h4>
                                    )}
                                    {el.name === 'image' && el.value !== '' && (
                                        <div>
                                            <img
                                                src={el.value ?? ''}
                                                alt="Item Image"
                                                style={{
                                                    width: '100%',
                                                    height: '100%',
                                                    objectFit: 'cover',
                                                }}
                                            />
                                        </div>
                                    )}
                                </div>
                            </Rnd>
                        ))}
                </div>
            )
        })
    }

    return (
        <div
            ref={imageDownloadRef as React.LegacyRef<HTMLDivElement>}
            className={styles.gridLayoutWrapper}
            style={{
                borderRadius: borderRadius,
                maxWidth: layoutWidth ?? 0,
                width: '100%',
                ...getBackgroundStyle(primaryColor),
            }}
        >
            <ResponsiveReactGridLayout
                draggableHandle=".drag-handle"
                containerPadding={[0, 0]}
                margin={[gridGap, gridGap]}
                autoSize={true}
                rowHeight={80}
                layouts={{
                    lg:
                        toolbox.lg.length > 0
                            ? toolbox.lg
                            : generateRowLayout(),
                }}
                cols={{
                    lg: Number(dynamicColumns || 3),
                    md: Number(dynamicColumns || 3),
                    sm: Number(dynamicColumns || 3),
                    xs: Number(dynamicColumns || 3),
                    xxs: 1,
                }}
                maxRows={Number(dynamicRows) || 1}
                breakpoints={{
                    lg: 1200,
                    md: 996,
                    sm: 768,
                    xs: 480,
                    xxs: 0,
                }}
                resizeHandles={['w', 'e', 'n', 's']}
                useCSSTransforms={mounted}
                onBreakpointChange={onBreakpointChange}
                onLayoutChange={onLayoutChange}
                onResizeStop={onResizeStop}
                isDroppable
            >
                {generateDOM()}
            </ResponsiveReactGridLayout>
        </div>
    )
}

export default SnapGrid
