import React, {useEffect, useRef, useState} from 'react'
import styles from './dashboardView.module.scss'
import { useLocation } from "react-router-dom";
import GetStarted from '@/components/dashboardView/elements/getStarted'
import SnapGrid from '@/components/dashboardView/elements/snapGrid'
import ConfigurationSidebar from '@/components/configration-menu/configurationSidebar'
import { ChevronLeft, ChevronRight } from 'lucide-react'

const DashboardView = () => {
    const useQuery = () => {
        return new URLSearchParams(useLocation().search);
    };
    const [layout, setLayout] = useState("");
    const query = useQuery();

    useEffect(() => {
        setLayout(query.get("layout") || '');
    }, [query]);

    const imageDownloadRef = useRef<HTMLDivElement | null>(null)
    const [isOpen, setIsOpen] = React.useState<boolean>(true)

    return (
        <div className={'flex w-full'}>
            {layout ? (
                <div className={styles.gridLayout}>
                    <SnapGrid imageDownloadRef={imageDownloadRef} />
                </div>
            ) : (
                <div className={styles.getStartWrapper}>
                    <GetStarted />
                </div>
            )}
            <div className={'relative'}>
                <ConfigurationSidebar
                    imageDownloadRef={imageDownloadRef}
                    isOpen={isOpen}
                />
                <div
                    className={styles.collapsesHandler}
                    onClick={() => setIsOpen(!isOpen)}
                >
                    {isOpen ? (
                        <ChevronRight size={16} />
                    ) : (
                        <ChevronLeft size={16} />
                    )}
                </div>
            </div>
        </div>
    )
}

export default DashboardView
