export const getBackgroundStyle = (color: string, transparent?: boolean) => {
    if (transparent) {
        return {
            backgroundColor: 'transparent',
            backdropFilter: 'blur(10px)',
        }
    } else {
        if (color && color.startsWith('linear-gradient')) {
            return {
                backgroundImage: color,
            }
        } else if (
            (color && color.startsWith('https://')) ||
            color.startsWith('http://') ||
            color.startsWith('data:image')
        ) {
            return {
                backgroundImage: `url(${color})`,
                backgroundSize: 'cover',
                backgroundPosition: 'center',
            }
        } else {
            return {
                backgroundColor: color,
            }
        }
    }
}

export const getColorStyle = (color: string) => {
    const style: Record<string, string> = {}

    if (color) {
        if (color.startsWith('linear-gradient')) {
            style.backgroundImage = color
            style.backgroundClip = 'text'
            style.WebkitBackgroundClip = 'text'
            style.color = 'transparent'
        } else if (
            color.startsWith('https://') ||
            color.startsWith('http://') ||
            color.startsWith('data:image')
        ) {
            style.backgroundImage = `url(${color})`
            style.backgroundClip = 'text'
            style.WebkitBackgroundClip = 'text'
            style.color = 'transparent'
        } else {
            style.color = color
        }
    }

    return style
}
