import React from 'react'
import { ImageItems } from '@/types/types'
import { Card } from '@/components/ui/card'
import { ChildCard } from '@/store/store'

interface ImagePickerPropTypes {
    id?: number | null
    data: ImageItems[]
    setColor?: (id: number, updatedFields: Partial<ChildCard>) => void
    setPrimaryColor?: (image: string) => void
}

const ImagePickerCard = ({
    id,
    data,
    setColor,
    setPrimaryColor,
}: ImagePickerPropTypes) => {
    const getColor = (image: string) => {
        return {
            backgroundImage: `url(${image})`,
            backgroundSize: 'cover',
            backgroundPosition: 'center',
        }
    }

    return (
        <div className={'max-h-[260px] h-full overflow-y-auto custom-scrollbar'}>
            <div className={'grid grid-cols-2 gap-2.5 justify-center'}>
                {data.map((el: ImageItems) => (
                    <Card
                        key={el.id}
                        className={'w-full h-14 rounded cursor-pointer'}
                        style={getColor(el.image)}
                        onClick={() => {
                            if (id && setColor) {
                                setColor(id, { backgroundImage: el.image })
                            } else if (setPrimaryColor) {
                                setPrimaryColor(el.image)
                            }
                        }}
                    />
                ))}
            </div>
        </div>
    )
}

export default ImagePickerCard
