import React, { useEffect, useState } from 'react'
import styles from './configurationSidebar.module.scss'
import useCardStore from '@/store/store'
import DefaultCustomization from '@/components/configration-menu/elements/defaultCustomization'
import SelectedBoxSettings from '@/components/configration-menu/elements/selectedBoxSettings'
import Empty from '@/components/empty-component/empty'

interface PropTypes {
    imageDownloadRef: React.RefObject<HTMLDivElement | null>
    isOpen: boolean
}

type FontVariant = '300' | 'regular' | '500' | '600' | '700' | '800'

interface FontData {
    family: string
    variants: FontVariant[]
    subsets: string[]
    version: string
    lastModified: string
    files: Record<FontVariant, string>
    category: string
    kind: string
    menu: string
}

const ConfigurationSidebar = ({ imageDownloadRef, isOpen }: PropTypes) => {
    const { selectedID } = useCardStore()
    const [allFonts, setAllFonts] = useState<string[]>([])

    useEffect(() => {
        fetch(
            `https://www.googleapis.com/webfonts/v1/webfonts?key=${import.meta.env.VITE_GOOGLE_API_KEY}`
        )
            .then((res) => res.json())
            .then((data) => {
                const fonts = data.items.map((font: FontData) => font.family)
                setAllFonts(fonts)

                const link = document.createElement('link');
                link.href = `https://fonts.googleapis.com/css2?family=${fonts
                    .map((f: any) => f.replace(/\s+/g, '+'))
                    .join('&family=')}&display=swap`;
                link.rel = 'stylesheet';
                document.head.appendChild(link);
            })
            .catch((error) => console.error('Error fetching fonts:', error))
    }, [])

    return (
        <div
            className={`${isOpen ? 'w-[300px]' : 'w-0'} ${styles.customizationParentWrapper} custom-scrollbar`}
        >
            <div
                className={`${isOpen ? `${styles.collapsesOpenItem} scale-1` : 'scale-0 opacity-10'}`}
            >
                <div className={`p-4`}>
                    <h5 className={'text-center'}>Default customization</h5>
                    <DefaultCustomization />
                </div>

                <div className={`p-4 border-t`}>
                    <h5 className={'text-center leading-4'}>
                        Selected Snap Box settings
                    </h5>
                    {selectedID ? (
                        <SelectedBoxSettings
                            imageDownloadRef={imageDownloadRef}
                            allFonts={allFonts}
                        />
                    ) : (
                        <div className={styles.emptyWrapper}>
                            <Empty message={'No selected box'} />
                        </div>
                    )}
                </div>
            </div>
        </div>
    )
}

export default ConfigurationSidebar
