import { create } from 'zustand'

export interface ChildrenItems {
    id: string
    name: string
    value: string | null
    fontSize?: number | null
    fontFamily: string
    color?: string
    direction?: {
        horizontal: number
        vertical: number
    }
    position?: {
        positionX: number
        positionY: number
    }
}

export interface ChildCard {
    id: number
    title: string
    titleColor: string
    titleSize: number
    fontFamily: string
    titleHorizontal: string
    titleVertical: string
    backgroundColor?: string | null
    backgroundImage?: string | null
    transparent: boolean
    children?: ChildrenItems[]
}

interface ParentCardState {
    primaryColor: string
    secondaryColor: string
    fontWeight: number
    borderRadius: number
    gridGap: number
    layoutWidth: number
    selectedID: number | null
    items: ChildCard[]

    setSelectedID: (id: number | null) => void
    setPrimaryColor: (color: string) => void
    setSecondaryColor: (color: string) => void
    setFontWeight: (weight: number) => void
    setBorderRadius: (radius: number) => void
    setGridGap: (gap: number) => void
    setLayoutWidth: (width: number) => void

    setItems: (newItems: ChildCard[]) => void
    addNewRow: () => void
    addItems: (child: ChildCard, id: number) => void
    updateItems: (
        id: number | string,
        updatedFields: Partial<ChildCard>
    ) => void
    updateChildItem: (
        cardId: number,
        childId: string,
        updatedFields: Partial<ChildrenItems>
    ) => void
    removeItems: (id: number) => void
}

const useCardStore = create<ParentCardState>((set) => ({
    primaryColor: '#60A5FA',
    secondaryColor: '#ffffff',
    fontWeight: 500,
    borderRadius: 12,
    gridGap: 10,
    layoutWidth: 1000,
    selectedID: null,
    items: [],

    setSelectedID: (id) =>
        set(() => ({
            selectedID: id,
        })),

    setPrimaryColor: (color) =>
        set(() => ({
            primaryColor: color,
        })),

    setSecondaryColor: (color) =>
        set(() => ({
            secondaryColor: color,
        })),

    setFontWeight: (weight) =>
        set(() => ({
            fontWeight: weight,
        })),

    setBorderRadius: (radius) =>
        set(() => ({
            borderRadius: radius,
        })),

    setLayoutWidth: (width) =>
        set(() => ({
            layoutWidth: width,
        })),

    setItems: (newItems) =>
        set(() => ({
            items: newItems,
        })),

    setGridGap: (gap) =>
        set(() => ({
            gridGap: gap,
        })),

    addNewRow: () =>
        set((state) => {
            const urlParams = new URLSearchParams(window.location.search)
            const currentRow = parseInt(urlParams.get('row') ?? '0') || 0
            const newRowNumber = Number(currentRow) + 1

            urlParams.set('row', newRowNumber.toString())
            window.history.pushState(
                {},
                '',
                `${window.location.pathname}?${urlParams}`
            )

            const newRow = {
                id: new Date().getTime(),
                title: 'Title',
                titleColor: state.primaryColor,
                titleSize: 30,
                fontFamily: 'Barlow',
                titleHorizontal: '10',
                titleVertical: '10',
                backgroundColor: state.secondaryColor,
                backgroundImage: null,
                transparent: false,
                children: [],
            }

            const newItems = [...state.items, newRow]
            localStorage.setItem('snapGridItems', JSON.stringify(newItems))

            return { items: newItems }
        }),

    addItems: (child, selectedIndex) =>
        set((state) => {
            const newItems = state.items.map((item) => ({ ...item })) // Deep clone objects
            newItems.splice(selectedIndex, 0, { ...child }) // Clone new child before adding

            localStorage.setItem('snapGridItems', JSON.stringify(newItems))
            return { items: newItems }
        }),

    updateItems: (id, updatedFields) =>
        set((state) => {
            const updatedItems = state.items.map((item) =>
                item.id === id
                    ? {
                          ...item,
                          ...updatedFields,
                          children: updatedFields.children
                              ? updatedFields.children
                              : item.children,
                      }
                    : item
            )
            localStorage.setItem('snapGridItems', JSON.stringify(updatedItems))

            return { items: updatedItems }
        }),

    updateChildItem: (
        cardId: number,
        childId: string,
        updatedFields: Partial<ChildrenItems>
    ) =>
        set((state) => {
            const updatedItems = state.items.map((card) =>
                card.id === cardId
                    ? {
                          ...card,
                          children: card.children?.map((child) =>
                              child.id === childId
                                  ? { ...child, ...updatedFields }
                                  : child
                          ),
                      }
                    : card
            )

            localStorage.setItem('snapGridItems', JSON.stringify(updatedItems))

            return { items: updatedItems }
        }),

    removeItems: (id: number) =>
        set((state) => {
            const updatedItems = state.items.filter((child) => child.id !== id)
            localStorage.setItem('snapGridItems', JSON.stringify(updatedItems))

            return { items: updatedItems }
        }),
}))

export default useCardStore
