import React, { useCallback } from 'react'
import {
    Dialog,
    DialogContent,
    DialogDescription,
    DialogFooter,
    DialogHeader,
    DialogTitle,
    DialogTrigger,
} from '@/components/ui/dialog'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { SubmitHandler, useForm } from 'react-hook-form'
import { v4 as uuidv4 } from 'uuid'
import {useNavigate} from "react-router-dom";

interface InputTypes {
    row: number
    col: number
}

const CustomLayoutGenerateModal = () => {
    const navigate = useNavigate()
    const form = useForm<InputTypes>()
    const { register, handleSubmit } = form

    const onSubmit: SubmitHandler<InputTypes> = useCallback(
        async (data: InputTypes) => {
            navigate(`?layout=${uuidv4()}&col=${data.col}&row=${data.row}`)
            localStorage.removeItem('snapGridLayout')
            localStorage.removeItem('snapGridItems')
        },
        [navigate]
    )

    return (
        <Dialog>
            <DialogTrigger asChild>
                <Button>Custom Layout</Button>
            </DialogTrigger>
            <DialogContent className="sm:max-w-[425px]">
                <form onSubmit={handleSubmit(onSubmit)}>
                    <DialogHeader>
                        <DialogTitle>Choose your layout</DialogTitle>
                        <DialogDescription>
                            Create customized layouts perfectly suited to your
                            specific needs and preferences.
                        </DialogDescription>
                    </DialogHeader>
                    <div className="grid gap-4 py-4">
                        <Input
                            id="column"
                            type={'number'}
                            max={20}
                            min={1}
                            {...register('col')}
                            required={true}
                            placeholder={'Column (3)'}
                            className="col-span-4"
                        />
                        <Input
                            id="row"
                            type={'number'}
                            required={true}
                            max={9}
                            min={1}
                            {...register('row')}
                            placeholder={'Row (3)'}
                            className="col-span-4"
                        />
                    </div>
                    <DialogFooter>
                        <Button type="submit" className={'w-full'}>
                            Generate Layout
                        </Button>
                    </DialogFooter>
                </form>
            </DialogContent>
        </Dialog>
    )
}

export default CustomLayoutGenerateModal
