import React from 'react'
import { ColorItems } from '@/types/types'
import { Card } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Sparkles } from 'lucide-react'
import { Input } from '@/components/ui/input'

interface ColorPickerPropTypes {
    data: ColorItems[]
    setColor: (color: string) => void
    handleRandomPick: (data: ColorItems[]) => void
    isGradient?: boolean
}

const ColorPickerCard = ({
    data,
    setColor,
    handleRandomPick,
    isGradient,
}: ColorPickerPropTypes) => {
    const getColor = (color: string) => {
        if (isGradient) {
            return {
                backgroundImage: color,
                backgroundSize: 'cover',
                backgroundPosition: 'center',
            }
        } else {
            return {
                backgroundColor: color,
            }
        }
    }
    return (
        <div>
            <div
                className={'flex items-center gap-2.5 flex-wrap justify-center'}
            >
                {data.map((el: ColorItems) => (
                    <Card
                        key={el.id}
                        className={'w-7 h-7 rounded cursor-pointer'}
                        style={getColor(el.color)}
                        onClick={() => setColor(el.color)}
                    />
                ))}
            </div>
            <div className={'mt-4'}>
                <Button
                    variant={'outline'}
                    className={'w-full'}
                    onClick={() => handleRandomPick(data)}
                >
                    Pick Random <Sparkles />
                </Button>
            </div>
            {!isGradient && (
                <div className={'mt-4 flex items-center gap-5 justify-end'}>
                    <p>Custom:</p>
                    <Input
                        type={'color'}
                        className={'w-14'}
                        onChange={(e) => {
                            setColor(e.target.value)
                        }}
                    />
                </div>
            )}
        </div>
    )
}

export default ColorPickerCard
