 

import React, { useEffect, useState } from 'react'
import {
    Dialog,
    DialogContent,
    DialogDescription,
    DialogFooter,
    DialogHeader,
    DialogTitle,
    DialogTrigger,
} from '@/components/ui/dialog'
import { Button } from '@/components/ui/button'
import { Plus } from 'lucide-react'
import { Label } from '@/components/ui/label'
import { Input } from '@/components/ui/input'
import useCardStore, { ChildrenItems } from '@/store/store'
import { v4 as uuidv4 } from 'uuid'

const ElementAddDialog = () => {
    const { items, selectedID, updateItems } = useCardStore()

    const selectedElement = items.find((item) => item.id === selectedID)

    const [errors, setErrors] = useState<Record<string, string>>({})
    const [isOpen, setIsOpen] = useState<boolean>(false)

    const [elements, setElements] = useState<ChildrenItems[]>([
        { id: uuidv4(), name: 'text', value: '', fontSize: 16, fontFamily: '' },
    ])
    const [imageElement, setImageElement] = useState<ChildrenItems[]>([
        {
            id: uuidv4(),
            name: 'image',
            value: '',
            fontSize: null,
            fontFamily: '',
        },
    ])

    useEffect(() => {
        if (selectedID !== null && selectedElement?.children) {
            // eslint-disable-next-line @typescript-eslint/no-unused-expressions
            selectedElement?.children?.length > 0 &&
                selectedElement.children.map((el) => {
                    if (el.name === 'text') {
                        setElements(() => [
                            {
                                id: el.id,
                                name: 'text',
                                value: el.value,
                                fontSize: el?.fontSize ?? 16,
                                fontFamily: el.fontFamily ?? '',
                            },
                        ])
                    } else if (el.name === 'image') {
                        setImageElement(() => [
                            {
                                id: el.id,
                                name: 'image',
                                value: el.value,
                                fontSize: null,
                                fontFamily: '',
                            },
                        ])
                    }
                })
        }
    }, [selectedID])

    const addElement = () => {
        setElements((prev) => [
            ...prev,
            {
                id: uuidv4(),
                name: 'text',
                value: '',
                fontSize: 16,
                fontFamily: '',
            },
        ])
    }

    const addImageElement = () => {
        setImageElement((prev) => [
            ...prev,
            {
                id: uuidv4(),
                name: 'image',
                value: '',
                fontSize: null,
                fontFamily: '',
            },
        ])
    }

    const handleElementChange = (id: string, field: string, value: string) => {
        setElements((prev) =>
            prev.map((element) =>
                element.id === id ? { ...element, field, value } : element
            )
        )
    }

    const handleFileChange = (
        id: number | string,
        event: React.ChangeEvent<HTMLInputElement>
    ) => {
        if (
            id &&
            event &&
            event.target.files &&
            event.target.files.length > 0
        ) {
            const file = event.target.files[0]
            const allowedTypes = [
                'image/png',
                'image/jpg',
                'image/jpeg',
                'image/webp',
                'image/avif',
            ]

            const render = new FileReader()

            if (file && !allowedTypes.includes(file.type)) {
                setErrors((prev) => ({
                    ...prev,
                    [id]: 'Only PNG, JPG, JPEG, WEBP, and AVIF file formats are allowed',
                }))
            } else {
                setErrors((prev) => ({ ...prev, [id]: '' }))
                render.onload = () => {
                    if (render?.result !== null) {
                        setImageElement((prev) =>
                            prev.map((image) =>
                                image.id === id
                                    ? {
                                          ...image,
                                          name: 'image',
                                          value:
                                              render?.result !== null
                                                  ? render?.result.toString()
                                                  : null,
                                          fontSize: null,
                                      }
                                    : image
                            )
                        )
                    } else {
                        setErrors((prev) => ({
                            ...prev,
                            [id]: 'Failed to load image',
                        }))
                    }
                }
                render.readAsDataURL(file)
            }
        }
    }

    const addItemsElement = () => {
        if (selectedID) {
            const updatedChildren = elements.map((el) => ({
                id: el.id,
                name: el.name,
                value: el.value,
                fontSize: el.fontSize,
                fontFamily: 'Barlow',
            }))

            const updateImageElement = imageElement.map((el) => ({
                id: el.id,
                name: el.name,
                value: el.value,
                fontSize: null,
                fontFamily: 'Barlow',
            }))

            updatedChildren.push(...updateImageElement)

            const updatedItem = {
                id: selectedID,
                children: updatedChildren,
            }

            updateItems(selectedID, { children: updatedItem.children })
            setIsOpen(false)
        }
    }

    return (
        <>
            <Dialog open={isOpen} onOpenChange={setIsOpen}>
                <DialogTrigger asChild>
                    <Plus
                        size={20}
                        color={'#777'}
                        onClick={() => setIsOpen(true)}
                    />
                </DialogTrigger>
                <DialogContent className="sm:max-w-[425px]">
                    <DialogHeader>
                        <DialogTitle>Edit Grid Item</DialogTitle>
                        <DialogDescription>
                            Make changes to your grid here.
                        </DialogDescription>
                    </DialogHeader>
                    <div className="py-4 w-full max-h-[40vh] h-auto overflow-y-auto custom-scrollbar">
                        <div>
                            <Label>Text</Label>
                            {elements.map((element, index) => (
                                <div
                                    key={index}
                                    className={'flex items-center gap-3 mt-2'}
                                >
                                    <Input
                                        defaultValue={element.value ?? ''}
                                        placeholder={'Text'}
                                        onChange={(e) =>
                                            handleElementChange(
                                                element.id,
                                                `name`,
                                                e.target.value
                                            )
                                        }
                                    />
                                    <Button
                                        variant={'outline'}
                                        onClick={addElement}
                                    >
                                        <Plus />
                                    </Button>
                                </div>
                            ))}
                        </div>
                        <div className={'mt-5'}>
                            <Label>Image</Label>
                            {imageElement.map((element, index) => (
                                <div key={index}>
                                    <div
                                        className={
                                            'flex items-center gap-3 mt-2'
                                        }
                                    >
                                        <Input
                                            id="picture"
                                            type="file"
                                            className={`${element.value !== '' ? (errors[element.id] ? 'bg-red-50' : 'bg-green-50') : 'bg-transparent'}`}
                                            onChange={(event) =>
                                                handleFileChange(
                                                    element.id,
                                                    event
                                                )
                                            }
                                        />
                                        <Button
                                            variant={'outline'}
                                            onClick={addImageElement}
                                        >
                                            <Plus />
                                        </Button>
                                    </div>
                                    {errors[element.id] && (
                                        <p className="text-red-600 text-sm pt-1.5">
                                            {errors[element.id]}
                                        </p>
                                    )}
                                </div>
                            ))}
                        </div>
                    </div>
                    <DialogFooter>
                        <Button type="submit" onClick={addItemsElement}>
                            Add Element
                        </Button>
                    </DialogFooter>
                </DialogContent>
            </Dialog>
        </>
    )
}

export default ElementAddDialog
