import React from 'react'
import styles from './defaultCustomization.module.scss'
import { ColorItems } from '@/types/types'
import { defaultImages, gradientColors, pickColor } from '@/json/defaultColors'
import useCardStore from '@/store/store'
import { Card } from '@/components/ui/card'
import {
    Popover,
    PopoverContent,
    PopoverTrigger,
} from '@/components/ui/popover'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { Slider } from '@/components/ui/slider'
import ColorPickerCard from '@/components/configration-menu/elements/colorPickerCard'
import { Input } from '@/components/ui/input'
import { getBackgroundStyle } from '@/utils/getStyles'
import ImagePickerCard from '@/components/configration-menu/elements/ImagePickerCard'

const DefaultCustomization = () => {
    const {
        gridGap,
        borderRadius,
        fontWeight,
        primaryColor,
        secondaryColor,
        layoutWidth,
        setPrimaryColor,
        setSecondaryColor,
        setFontWeight,
        setBorderRadius,
        setGridGap,
        setLayoutWidth,
    } = useCardStore()

    const [tooltip, setTooltip] = React.useState(false)
    const [tooltip2, setTooltip2] = React.useState(false)
    const [tooltip3, setTooltip3] = React.useState(false)

    const handleRandomPick = (data: ColorItems[]) => {
        const randomIndex = Math.floor(Math.random() * data.length)
        setPrimaryColor(data[randomIndex].color)
    }

    const handleRandomSecondaryPick = (data: ColorItems[]) => {
        const randomIndex = Math.floor(Math.random() * data.length)
        setSecondaryColor(data[randomIndex].color)
    }

    return (
        <div className={'mt-3'}>
            <div className={styles.itemWrapper}>
                <p>Primary Color</p>
                <Popover>
                    <PopoverTrigger asChild>
                        <Card
                            className={styles.itemColorCard}
                            style={getBackgroundStyle(primaryColor)}
                        />
                    </PopoverTrigger>
                    <PopoverContent className="w-80">
                        <Tabs defaultValue="solid" className={'w-full'}>
                            <TabsList className="grid w-full grid-cols-3">
                                <TabsTrigger value="solid">Solid</TabsTrigger>
                                <TabsTrigger value="gradient">
                                    Gradient
                                </TabsTrigger>
                                <TabsTrigger value="image">Image</TabsTrigger>
                            </TabsList>
                            <TabsContent value="solid">
                                <ColorPickerCard
                                    data={pickColor}
                                    setColor={setPrimaryColor}
                                    handleRandomPick={handleRandomPick}
                                />
                            </TabsContent>
                            <TabsContent value="gradient">
                                <ColorPickerCard
                                    data={gradientColors}
                                    setColor={setPrimaryColor}
                                    handleRandomPick={handleRandomPick}
                                    isGradient={true}
                                />
                            </TabsContent>
                            <TabsContent value="image">
                                <ImagePickerCard
                                    data={defaultImages}
                                    setPrimaryColor={setPrimaryColor}
                                />
                            </TabsContent>
                        </Tabs>
                    </PopoverContent>
                </Popover>
            </div>
            <div className={`${styles.itemWrapper} mt-2`}>
                <p>Secondary Color</p>
                <Popover>
                    <PopoverTrigger asChild>
                        <Card
                            className={styles.itemColorCard}
                            style={getBackgroundStyle(secondaryColor)}
                        />
                    </PopoverTrigger>
                    <PopoverContent className="w-80">
                        <Tabs defaultValue="solid" className={'w-full'}>
                            <TabsList className="grid w-full grid-cols-2">
                                <TabsTrigger value="solid">Solid</TabsTrigger>
                                <TabsTrigger value="gradient">
                                    Gradient
                                </TabsTrigger>
                            </TabsList>
                            <TabsContent value="solid">
                                <ColorPickerCard
                                    data={pickColor}
                                    setColor={setSecondaryColor}
                                    handleRandomPick={handleRandomSecondaryPick}
                                />
                            </TabsContent>
                            <TabsContent value="gradient">
                                <ColorPickerCard
                                    data={gradientColors}
                                    setColor={setSecondaryColor}
                                    handleRandomPick={handleRandomSecondaryPick}
                                    isGradient={true}
                                />
                            </TabsContent>
                        </Tabs>
                    </PopoverContent>
                </Popover>
            </div>
            <div className={`${styles.itemWrapper} mt-2`}>
                <p>Font Weight</p>
                <div className={'w-36 relative'}>
                    <Slider
                        defaultValue={[500]}
                        max={700}
                        min={100}
                        step={100}
                        onValueChange={(newWeight) => {
                            setFontWeight(newWeight[0])
                        }}
                        onMouseEnter={() => setTooltip(true)}
                        onMouseLeave={() => setTooltip(false)}
                    />
                    {tooltip && (
                        <span className={styles.customTooltip}>
                            {fontWeight}
                        </span>
                    )}
                </div>
            </div>
            <div className={`${styles.itemWrapper} mt-2`}>
                <p>Border Radius</p>
                <div className={'w-36 relative'}>
                    <Slider
                        defaultValue={[12]}
                        max={50}
                        min={1}
                        step={1}
                        onValueChange={(newRadius) => {
                            setBorderRadius(newRadius[0])
                        }}
                        onMouseEnter={() => setTooltip2(true)}
                        onMouseLeave={() => setTooltip2(false)}
                    />
                    {tooltip2 && (
                        <span className={styles.customTooltip}>
                            {borderRadius}
                        </span>
                    )}
                </div>
            </div>
            <div className={`${styles.itemWrapper} mt-2`}>
                <p>Gap</p>
                <div className={'w-36 relative'}>
                    <Slider
                        defaultValue={[10]}
                        max={20}
                        min={1}
                        step={1}
                        onValueChange={(newGrid) => {
                            setGridGap(newGrid[0])
                        }}
                        onMouseEnter={() => setTooltip3(true)}
                        onMouseLeave={() => setTooltip3(false)}
                    />
                    {tooltip3 && (
                        <span className={styles.customTooltip}>{gridGap}</span>
                    )}
                </div>
            </div>
            <div className={`${styles.itemWrapper} mt-2`}>
                <p>Width</p>
                <div className={'w-36'}>
                    <Input
                        type={'number'}
                        defaultValue={layoutWidth}
                        placeholder={'Width'}
                        onChange={(e) =>
                            setLayoutWidth(parseInt(e.target.value))
                        }
                    />
                </div>
            </div>
        </div>
    )
}

export default DefaultCustomization
