import React from 'react'
import { ColorItems } from '@/types/types'
import { ChildCard, ChildrenItems } from '@/store/store'
import { Card } from '@/components/ui/card'
import { Input } from '@/components/ui/input'

interface ColorPickerPropTypes {
    id: number | null
    childId?: string
    data: ColorItems[]
    setColor?: (id: number, updatedFields: Partial<ChildCard>) => void
    setChildColor?: (
        id: number,
        childId: string,
        updateChildItem: Partial<ChildrenItems>
    ) => void
    isGradient?: boolean
    fieldName: string
    textColor?: boolean
}

const ColorPickerCardBg = ({
    id,
    childId,
    data,
    textColor,
    setColor,
    setChildColor,
    isGradient,
    fieldName,
}: ColorPickerPropTypes) => {
    const getColor = (color: string) => {
        if (isGradient) {
            return {
                backgroundImage: color,
                backgroundSize: 'cover',
                backgroundPosition: 'center',
            }
        } else {
            return {
                backgroundColor: color,
            }
        }
    }
    return (
        <div>
            <div
                className={'flex items-center gap-2.5 flex-wrap justify-center'}
            >
                {id &&
                    data.map((el: ColorItems) => (
                        <Card
                            key={el.id}
                            className={'w-7 h-7 rounded cursor-pointer'}
                            style={getColor(el.color)}
                            onClick={() => {
                                if (textColor) {
                                    if (childId && setChildColor) {
                                        setChildColor(id, childId, {
                                            [fieldName]: el.color,
                                        })
                                    } else if (setColor) {
                                        setColor(id, {
                                            [fieldName]: el.color,
                                        })
                                    }
                                } else {
                                    if (childId && setChildColor) {
                                        setChildColor(id, childId, {
                                            [fieldName]: el.color,
                                        })
                                    } else if (setColor) {
                                        setColor(id, {
                                            [fieldName]: el.color,
                                            backgroundImage: null,
                                        })
                                    }
                                }
                            }}
                        />
                    ))}
            </div>
            {!isGradient && id && (
                <div className={'mt-4 flex items-center gap-5 justify-end'}>
                    <p>Custom:</p>
                    <Input
                        type={'color'}
                        className={'w-14'}
                        onChange={(e) => {
                            if (textColor) {
                                if (childId && setChildColor) {
                                    setChildColor(id, childId, {
                                        [fieldName]: e.target.value,
                                    })
                                } else if (setColor) {
                                    setColor(id, {
                                        [fieldName]: e.target.value,
                                    })
                                }
                            } else {
                                if (childId && setChildColor) {
                                    setChildColor(id, childId, {
                                        [fieldName]: e.target.value,
                                    })
                                } else if (setColor) {
                                    setColor(id, {
                                        [fieldName]: e.target.value,
                                        backgroundImage: null,
                                    })
                                }
                            }
                        }}
                    />
                </div>
            )}
        </div>
    )
}

export default ColorPickerCardBg
