import React from 'react'
import { Grid2x2, Grid3x3, Table } from 'lucide-react'
import CustomLayoutGenerateModal from '@/components/dashboardView/elements/customLayoutGenerateModal'
import { v4 as uuidv4 } from 'uuid'
import { useNavigate } from 'react-router-dom'

const GetStarted = () => {
    const navigate = useNavigate()
    return (
        <>
            <div className={'flex flex-col gap-3 items-center'}>
                <p>Choose a layout to get started</p>
                <div className={'flex gap-2 items-center'}>
                    <Grid2x2
                        color={'#5555ff'}
                        strokeWidth={1}
                        className={'cursor-pointer'}
                        onClick={() => {
                            navigate(`?layout=${uuidv4()}&col=2&row=2`)
                            localStorage.removeItem('snapGridLayout');
                            localStorage.removeItem('snapGridItems');
                        }}
                    />
                    <Table
                        color={'#5555ff'}
                        strokeWidth={1}
                        className={'cursor-pointer'}
                        onClick={() => {
                            navigate(`?layout=${uuidv4()}&col=2&row=3`)
                            localStorage.removeItem('snapGridLayout');
                            localStorage.removeItem('snapGridItems');
                        }}
                    />
                    <Grid3x3
                        color={'#5555ff'}
                        strokeWidth={1}
                        className={'cursor-pointer'}
                        onClick={() => {
                            navigate(`?layout=${uuidv4()}&col=3&row=3`)
                            localStorage.removeItem('snapGridLayout');
                            localStorage.removeItem('snapGridItems');
                        }}
                    />
                </div>
                <p>OR</p>
                <CustomLayoutGenerateModal />
            </div>
        </>
    )
}

export default GetStarted
