import React, { useCallback, useEffect } from 'react'
import styles from './selectedBoxSettings.module.scss'
import ColorPickerCardBg from '@/components/configration-menu/elements/colorPickerCardBg'
import ImagePickerCard from '@/components/configration-menu/elements/ImagePickerCard'
import SliderWithTooltip from '@/components/configration-menu/elements/sliderWithTooltip'
import { Button } from '@/components/ui/button'
import { Card } from '@/components/ui/card'
import { Checkbox } from '@/components/ui/checkbox'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import {
    Popover,
    PopoverContent,
    PopoverTrigger,
} from '@/components/ui/popover'
import { Slider } from '@/components/ui/slider'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { defaultImages, gradientColors, pickColor } from '@/json/defaultColors'
import useCardStore from '@/store/store'
import { getBackgroundStyle } from '@/utils/getStyles'
import { toPng } from 'html-to-image'
import {
    Select,
    SelectContent,
    SelectGroup,
    SelectItem,
    SelectLabel,
    SelectTrigger,
    SelectValue,
} from '@/components/ui/select'
import { FontFamily } from '@/json/fontFamily'

interface PropTypes {
    imageDownloadRef: React.RefObject<HTMLDivElement | null>
    allFonts: string[]
}

const SelectedBoxSettings = ({ imageDownloadRef, allFonts }: PropTypes) => {
    const {
        items,
        selectedID,
        primaryColor,
        addNewRow,
        updateItems,
        updateChildItem,
        removeItems,
        setSelectedID,
    } = useCardStore()

    const [error, setError] = React.useState('')
    const [tooltipOpen, setTooltipOpen] = React.useState(false)
    const [fontSize, setFontSize] = React.useState(30)
    const [title, setTitle] = React.useState('')
    const [fontFamily, setFontFamily] = React.useState('Barlow')
    const [nestedFontFamily, setNestedFontFamily] = React.useState({
        id: '',
        family: 'Barlow',
    })

    const selectedCard = items.find((item) => item.id === selectedID)

    useEffect(() => {
        if (selectedCard) {
            setTitle(selectedCard.title)
            setFontSize(selectedCard.titleSize)
            if (allFonts.includes(selectedCard?.fontFamily)) {
                setFontFamily(selectedCard?.fontFamily)
            } else {
                setFontFamily('Barlow')
            }

            if (selectedCard.children && selectedCard.children.length > 0) {
                const child = selectedCard.children.find(
                    (child) => child.name === 'text'
                )
                if (child) {
                    setNestedFontFamily({
                        id: child.id,
                        family: child.fontFamily,
                    })
                }
            }
        }
    }, [selectedCard])

    const handleUpdate = (field: string, value: string | number | boolean) => {
        if (selectedID !== null) {
            updateItems(selectedID, { [field]: value })
        }
    }

    const handleAddBox = () => {
        if (selectedID) {
            addNewRow()
        }
    }

    const handleAddRow = () => {
        addNewRow()
    }

    const handleRemoveBox = () => {
        if (selectedID) {
            removeItems(selectedID)
        }
    }

    const handleFileChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        if (selectedID && event.target.files && event.target.files.length > 0) {
            const file = event.target.files[0]
            const allowedTypes = [
                'image/png',
                'image/jpg',
                'image/jpeg',
                'image/webp',
                'image/avif',
            ]

            const render = new FileReader()

            if (file && !allowedTypes.includes(file.type)) {
                setError(
                    'Only allow PNG, JPG, JPEG, WEBP and AVIF file formats'
                )
            } else {
                setError('')
                render.onload = () => {
                    if (render.result) {
                        updateItems(selectedID, {
                            backgroundImage: render.result.toString(),
                        })
                    }
                }
                render.readAsDataURL(file)
            }
        }
    }

    const handleDownloadClick = useCallback(() => {
        if (imageDownloadRef.current === null) {
            return
        }

        setSelectedID(null)
        toPng(imageDownloadRef.current, { cacheBust: true })
            .then((dataUrl) => {
                const link = document.createElement('a')
                link.download = 'snap-grid.png'
                link.href = dataUrl
                link.click()
            })
            .catch((err) => {
                console.log(err)
            })
    }, [imageDownloadRef])

    return (
        <div className={'mt-5'}>
            <div className={styles.buttonWrapper}>
                <Button size={'sm'} variant={'outline'} onClick={handleAddBox}>
                    Add Box
                </Button>
                <Button size={'sm'} variant={'outline'} onClick={handleAddRow}>
                    Add Row
                </Button>
                <Button
                    size={'sm'}
                    variant={'outline'}
                    onClick={handleRemoveBox}
                >
                    Remove Box
                </Button>
            </div>
            <div>
                <div>
                    <Label className={styles.inputLabel}>Title</Label>
                    <Input
                        type={'text'}
                        defaultValue={title}
                        placeholder={'Title'}
                        onChange={(e) => handleUpdate('title', e.target.value)}
                    />
                </div>
                <div className={styles.fontSizeWrapper}>
                    <p>Color</p>
                    <span>
                        <Popover>
                            <PopoverTrigger asChild>
                                <Card
                                    className={styles.itemColorCard}
                                    style={getBackgroundStyle(
                                        selectedCard?.titleColor ?? primaryColor
                                    )}
                                />
                            </PopoverTrigger>
                            <PopoverContent className="w-80">
                                <Tabs defaultValue="solid" className={'w-full'}>
                                    <TabsList className="grid w-full grid-cols-2">
                                        <TabsTrigger value="solid">
                                            Solid
                                        </TabsTrigger>
                                        <TabsTrigger value="gradient">
                                            Gradient
                                        </TabsTrigger>
                                    </TabsList>
                                    <TabsContent value="solid">
                                        <ColorPickerCardBg
                                            textColor={true}
                                            id={selectedID}
                                            data={pickColor}
                                            setColor={updateItems}
                                            fieldName={'titleColor'}
                                        />
                                    </TabsContent>
                                    <TabsContent value="gradient">
                                        <ColorPickerCardBg
                                            textColor={true}
                                            id={selectedID}
                                            isGradient={true}
                                            data={gradientColors}
                                            setColor={updateItems}
                                            fieldName={'titleColor'}
                                        />
                                    </TabsContent>
                                </Tabs>
                            </PopoverContent>
                        </Popover>
                    </span>
                </div>
                <div className={styles.fontSizeWrapper}>
                    <p>Title Size</p>
                    <div className={styles.sliderWrapper}>
                        <Slider
                            defaultValue={[fontSize]}
                            max={70}
                            min={14}
                            step={1}
                            onValueChange={(value) => {
                                handleUpdate('titleSize', value[0])
                                setFontSize(value[0])
                            }}
                            onMouseEnter={() => setTooltipOpen(true)}
                            onMouseLeave={() => setTooltipOpen(false)}
                        />
                        {tooltipOpen && (
                            <span className={styles.customTooltip}>
                                {fontSize}
                            </span>
                        )}
                    </div>
                </div>
                <div className={styles.fontSizeWrapper}>
                    <div className={'w-full'}>
                        <Select
                            onValueChange={(e) => {
                                setFontFamily(e)
                                updateItems(Number(selectedID), {
                                    fontFamily: e,
                                })
                            }}
                        >
                            <SelectTrigger className="w-full">
                                <SelectValue placeholder="Select Font Family" />
                            </SelectTrigger>
                            <SelectContent>
                                <SelectGroup>
                                    {allFonts
                                        .filter((el: string) =>
                                            FontFamily.includes(el)
                                        )
                                        .map((el: string) => (
                                            <SelectItem
                                                style={{
                                                    fontWeight: 700,
                                                    fontFamily: `"${el}", sans-serif`,
                                                }}
                                                key={el}
                                                value={el}
                                            >
                                                {el}
                                            </SelectItem>
                                        ))}
                                </SelectGroup>
                            </SelectContent>
                        </Select>
                    </div>
                </div>
                <div className={styles.fontSizeWrapper}>
                    <p className={'whitespace-nowrap'}>Title Position</p>
                    <div className={styles.positionWrapper}>
                        <div className="relative">
                            <Input
                                prefix={'%'}
                                placeholder={'Top'}
                                type={'number'}
                                max={100}
                                min={1}
                                defaultValue={selectedCard?.titleVertical ?? 5}
                                className={'w-16 p-2 pr-6'}
                                onChange={(e) => {
                                    handleUpdate(
                                        'titleVertical',
                                        e.target.value
                                    )
                                }}
                            />
                            <span className="absolute inset-y-0 right-2 flex items-center text-gray-600">
                                %
                            </span>
                        </div>
                        <div className="relative">
                            <Input
                                placeholder={'Left'}
                                className={'w-16 p-2 pr-6'}
                                max={100}
                                min={1}
                                defaultValue={
                                    selectedCard?.titleHorizontal ?? 5
                                }
                                onChange={(e) => {
                                    handleUpdate(
                                        'titleHorizontal',
                                        e.target.value
                                    )
                                }}
                            />
                            <span className="absolute inset-y-0 right-2 flex items-center text-gray-600">
                                %
                            </span>
                        </div>
                    </div>
                </div>
            </div>
            {items.length > 0 &&
                items
                    .filter((item) => item.children && item.children.length > 0)
                    .flatMap((item) =>
                        item.children
                            ?.filter((child) => child.name === 'text')
                            .map((child) => ({
                                parentId: item.id,
                                ...child,
                            }))
                    )
                    .map((filteredChild, index) =>
                        filteredChild ? (
                            <div
                                key={index}
                                className={'mt-5 border-t border-dashed pt-5'}
                            >
                                <div>
                                    <Label className={styles.inputLabel}>
                                        Item {index + 1}
                                    </Label>
                                    <Input
                                        type={'text'}
                                        defaultValue={
                                            filteredChild?.value ?? ''
                                        }
                                        placeholder={'Subtitle'}
                                        onChange={(e) => {
                                            if (selectedID) {
                                                updateChildItem(
                                                    Number(selectedID),
                                                    filteredChild.id,
                                                    {
                                                        value: e.target.value,
                                                    }
                                                )
                                            }
                                        }}
                                    />
                                </div>
                                <div className={styles.fontSizeWrapper}>
                                    <p>Color</p>
                                    <span>
                                        <Popover>
                                            <PopoverTrigger asChild>
                                                <Card
                                                    className={
                                                        styles.itemColorCard
                                                    }
                                                    style={getBackgroundStyle(
                                                        filteredChild?.color ??
                                                            primaryColor
                                                    )}
                                                />
                                            </PopoverTrigger>
                                            <PopoverContent className="w-80">
                                                <Tabs
                                                    defaultValue="solid"
                                                    className={'w-full'}
                                                >
                                                    <TabsList className="grid w-full grid-cols-2">
                                                        <TabsTrigger value="solid">
                                                            Solid
                                                        </TabsTrigger>
                                                        <TabsTrigger value="gradient">
                                                            Gradient
                                                        </TabsTrigger>
                                                    </TabsList>
                                                    <TabsContent value="solid">
                                                        <ColorPickerCardBg
                                                            textColor={true}
                                                            id={selectedID}
                                                            data={pickColor}
                                                            childId={
                                                                filteredChild.id
                                                            }
                                                            setChildColor={
                                                                updateChildItem
                                                            }
                                                            fieldName={'color'}
                                                        />
                                                    </TabsContent>
                                                    <TabsContent value="gradient">
                                                        <ColorPickerCardBg
                                                            textColor={true}
                                                            id={selectedID}
                                                            isGradient={true}
                                                            data={
                                                                gradientColors
                                                            }
                                                            childId={
                                                                filteredChild.id
                                                            }
                                                            setChildColor={
                                                                updateChildItem
                                                            }
                                                            fieldName={'color'}
                                                        />
                                                    </TabsContent>
                                                </Tabs>
                                            </PopoverContent>
                                        </Popover>
                                    </span>
                                </div>
                                <div className={styles.fontSizeWrapper}>
                                    <p>Text Size</p>
                                    <div className={styles.sliderWrapper}>
                                        <SliderWithTooltip
                                            childId={filteredChild?.id}
                                            defaultValue={
                                                filteredChild?.fontSize
                                            }
                                        />
                                    </div>
                                </div>
                                <div className={styles.fontSizeWrapper}>
                                    <div className={'w-full'}>
                                        <Select
                                            onValueChange={(e) => {
                                                setNestedFontFamily({
                                                    id: filteredChild.id,
                                                    family: e,
                                                })
                                                updateChildItem(
                                                    Number(selectedID),
                                                    filteredChild.id,
                                                    {
                                                        fontFamily: e,
                                                    }
                                                )
                                            }}
                                        >
                                            <SelectTrigger className="w-full">
                                                <SelectValue placeholder="Select Font Family" />
                                            </SelectTrigger>
                                            <SelectContent>
                                                <SelectGroup>
                                                    <SelectLabel>
                                                        {
                                                            nestedFontFamily.family
                                                        }
                                                    </SelectLabel>
                                                    {allFonts
                                                        .filter((el: string) =>
                                                            FontFamily.includes(
                                                                el
                                                            )
                                                        )
                                                        .map((el: string) => (
                                                            <SelectItem
                                                                style={{
                                                                    fontWeight: 700,
                                                                    fontFamily: `"${el}", sans-serif`,
                                                                }}
                                                                key={el}
                                                                value={el}
                                                            >
                                                                {el}
                                                            </SelectItem>
                                                        ))}
                                                </SelectGroup>
                                            </SelectContent>
                                        </Select>
                                    </div>
                                </div>
                            </div>
                        ) : null
                    )}

            <div className={'mt-5 border-t-2 border-dashed mb-16'}>
                <div className={styles.fontSizeWrapper}>
                    <p>Transparent Background</p>
                    <span>
                        <Checkbox
                            id="transparentBg"
                            defaultChecked={selectedCard?.transparent ?? false}
                            onCheckedChange={(value: boolean) => {
                                if (selectedID !== null) {
                                    updateItems(selectedID, {
                                        transparent: value,
                                    })
                                }
                            }}
                        />
                    </span>
                </div>
                <div className={styles.cardBgColor}>
                    <p>Background</p>
                    <Popover>
                        <PopoverTrigger asChild>
                            <Card
                                className={styles.itemColorCard}
                                style={getBackgroundStyle(
                                    selectedCard?.backgroundImage ??
                                        selectedCard?.backgroundColor ??
                                        ''
                                )}
                            />
                        </PopoverTrigger>
                        <PopoverContent className="w-80">
                            <Tabs defaultValue="solid" className={'w-full'}>
                                <TabsList className="grid w-full grid-cols-3">
                                    <TabsTrigger value="solid">
                                        Solid
                                    </TabsTrigger>
                                    <TabsTrigger value="gradient">
                                        Gradient
                                    </TabsTrigger>
                                    <TabsTrigger value="image">
                                        Image
                                    </TabsTrigger>
                                </TabsList>
                                <TabsContent value="solid">
                                    <ColorPickerCardBg
                                        id={selectedID}
                                        data={pickColor}
                                        setColor={updateItems}
                                        fieldName={'backgroundColor'}
                                    />
                                </TabsContent>
                                <TabsContent value="gradient">
                                    <ColorPickerCardBg
                                        id={selectedID}
                                        isGradient={true}
                                        data={gradientColors}
                                        setColor={updateItems}
                                        fieldName={'backgroundColor'}
                                    />
                                </TabsContent>
                                <TabsContent value="image">
                                    <ImagePickerCard
                                        id={selectedID}
                                        data={defaultImages}
                                        setColor={updateItems}
                                    />
                                    <div className={'mt-2'}>
                                        <Input
                                            id="picture"
                                            type="file"
                                            onChange={handleFileChange}
                                        />
                                        {error && (
                                            <p className="text-red-500 mt-2 text-sm">
                                                {error}
                                            </p>
                                        )}
                                    </div>
                                </TabsContent>
                            </Tabs>
                        </PopoverContent>
                    </Popover>
                </div>
            </div>
            <div className={'fixed bottom-0 w-[260px] pb-3 bg-[#fafafa] pt-2'}>
                <Button className={'w-full'} onClick={handleDownloadClick}>
                    Download
                </Button>
            </div>
        </div>
    )
}

export default SelectedBoxSettings
